import React, { useState } from "react";
import { Divider, Fab, Grid, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import HeaderWrapper from "components/Wrapper/HeaderWrapper";
import AddRoundedIcon from "@mui/icons-material/AddRounded";
import CreateEventDialog from "components/Header/CreateEventDialog";
import UserGroups from "containers/Profile/UserGroups";

const useStyles = makeStyles((theme) => ({
  dmHomeRoot: {
    padding: 10,
    height: "100%",
  },
  dmEventCard: {
    background: theme.palette.primary.main + "24",
    display: "flex",
    justifyContent: "space-between",
    borderRadius: 8,
    padding: 10,
    marginBottom: 10,
  },
  dmEventGroup: {
    background: "red",
    width: 10,
    height: 10,
    display: "block",
    borderRadius: "50%",
    marginLeft: 8,
  },
  dmEventNameInfo: {
    "&>h6": {
      fontWeight: 600,
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    },
    "&>p": {
      color: theme.palette.grey[500],
    },
  },
  dmEventDateContainer: {
    marginTop: 10,
  },
}));

function Home() {
  const classes = useStyles();

  const [createEventDialog, setCreateEventDialog] = useState(false);

  return (
    <HeaderWrapper>
      <Grid className={classes.dmHomeRoot}>
        <UserGroups />
        <Grid>
          <Grid className={classes.dmEventDateContainer}>
            <Typography variant="body2">20 Nov, 2021</Typography>
            <Grid>
              <Grid className={classes.dmEventCard}>
                <Grid className={classes.dmEventNameInfo}>
                  <Typography variant="subtitle1">
                    Jignesh Divani
                    <span className={classes.dmEventGroup}></span>
                  </Typography>
                  <Typography variant="body2">Completed 22 years</Typography>
                </Grid>
                <Grid>
                  <img
                    src={`${process.env.PUBLIC_URL}/assets/profile.png`}
                    alt="profile-img"
                  ></img>
                </Grid>
              </Grid>
              <Grid className={classes.dmEventCard}>
                <Grid className={classes.dmEventNameInfo}>
                  <Typography variant="subtitle1">
                    Jignesh Divani
                    <span className={classes.dmEventGroup}></span>
                  </Typography>
                  <Typography variant="body2">Completed 22 years</Typography>
                </Grid>
                <Grid>
                  <img
                    src={`${process.env.PUBLIC_URL}/assets/profile.png`}
                    alt="profile-img"
                  ></img>
                </Grid>
              </Grid>
            </Grid>
            <Divider className={classes.dmEventDivider} />
          </Grid>
          <Grid className={classes.dmEventDateContainer}>
            <Typography variant="body2">20 Nov, 2021</Typography>
            <Grid>
              <Grid className={classes.dmEventCard}>
                <Grid className={classes.dmEventNameInfo}>
                  <Typography variant="subtitle1">
                    Jignesh Divani
                    <span className={classes.dmEventGroup}></span>
                  </Typography>
                  <Typography variant="body2">Completed 22 years</Typography>
                </Grid>
                <Grid>
                  <img
                    src={`${process.env.PUBLIC_URL}/assets/profile.png`}
                    alt="profile-img"
                  ></img>
                </Grid>
              </Grid>
              <Grid className={classes.dmEventCard}>
                <Grid className={classes.dmEventNameInfo}>
                  <Typography variant="subtitle1">
                    Jignesh Divani
                    <span className={classes.dmEventGroup}></span>
                  </Typography>
                  <Typography variant="body2">Completed 22 years</Typography>
                </Grid>
                <Grid>
                  <img
                    src={`${process.env.PUBLIC_URL}/assets/profile.png`}
                    alt="profile-img"
                  ></img>
                </Grid>
              </Grid>
            </Grid>
            <Divider className={classes.dmEventDivider} />
          </Grid>
        </Grid>
        <Fab
          className="testing"
          sx={{
            position: "absolute",
            bottom: 16,
            right: 16,
          }}
          color="primary"
          onClick={() => {
            setCreateEventDialog(true);
          }}
        >
          <AddRoundedIcon />
        </Fab>
        <CreateEventDialog
          open={createEventDialog}
          handleClose={() => setCreateEventDialog(false)}
        />
      </Grid>
    </HeaderWrapper>
  );
}

export default Home;
