import { bindReducer, CreateBase } from 'easy-redux-http-call'

const { createStore, combineReducers, compose } = require("redux");
const { default: userReducer } = require("./redux/userReducer");

const rootReducer = combineReducers({
  user: bindReducer(userReducer, { key: "user" })
})

// For Local viewing the devtool
function extensionComposeStub(funcs) {
  if (funcs.length === 0) return undefined;
  if (typeof funcs[0] === 'object') return compose;
  return compose(...(funcs));
}

const composeWithDevTools =
  typeof window !== 'undefined' && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    : extensionComposeStub;

const store = createStore(rootReducer,
  composeWithDevTools()
)

export const base = new CreateBase({
  store: store,
  baseApiUrl: process.env.REACT_APP_API_URL,
  defaultState: {},
  httpFn: (data) => {
    return window.fetch(data.endpoint, {
      method: data.method,
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${window.localStorage.getItem('token')}`,
        ...data.headers || {},
      },
      ...(Object.keys(data.params || {}).length && {
        body: JSON.stringify(data.params)
      })
    })
      .then(async (response) => {
        let data = await response.json()
        return {
          data: data,
          status: response.status
        }
      })
  }
});

export default store