import PageNotFound from "containers/404/PageNotFound";
import Home from "containers/Home/Home";
import SuspenseFallback from "containers/SuspenseFallback/SuspenseFallback";
import React, { lazy, Suspense } from "react";
import { HashRouter, Route, Routes } from "react-router-dom";

const Login = lazy(() => import("./containers/Login/Login"));
const Signup = lazy(() => import("./containers/Signup/Signup"));
const Profile = lazy(() => import("./containers/Profile/Profile"));
const ForgotPassword = lazy(() =>
  import("./containers/ForgotPassword/ForgotPassword")
);
const HomeLanding = lazy(() =>
  import("./containers/LandingPage/LandingPage")
);

export default function AppRoutes() {

  return (
    <div>
      <Suspense fallback={<SuspenseFallback />}>
        <HashRouter>
          <Routes>
            <Route exact path="/login" element={<Login />} />
            <Route exact path="/signup" element={<Signup />} />
            <Route exact path="/forgot-password" element={<ForgotPassword />} />
            <Route exact path="/home" element={<Home />} />
            <Route exact path="/profile" element={<Profile />} />
            <Route exact path="/" element={<HomeLanding />} />

            <Route exact path="*" element={<PageNotFound />} />
          </Routes>
        </HashRouter>
      </Suspense>
    </div>
  );
}
