import { base } from "store/store";

export const AI_create_group = base.createInstance({
  endpoint: "/group",
  reducer: "user",
  reduxKey: "createGroup",
  action: "CREATE_GROUP",
  method: "post",
  onSuccess: (res) => {
    AI_get_group.updateAction([res.data])
  }
});

export const AI_get_group = base.createListInstance({
  endpoint: "/group",
  reducer: "user",
  reduxKey: "getGrouop",
  key: "_id",
  action: "GET_GROUPS",
  method: "get",
  onSuccess: (res) => res.data
});

export const AI_update_system = base.createInstance({
  endpoint: "/group/{id}",
  reducer: "user",
  reduxKey: "updateGroup",
  action: "UPDATE_GROUP",
  method: "put",
  onSuccess: (res) => {
    AI_get_group.updateAction([res.data])
  }
})

export const AI_delete_system = base.createInstance({
  endpoint: "/group/{id}",
  reducer: "user",
  reduxKey: "deleteGroup",
  action: "DELETE_GROUP",
  method: "delete",
  onSuccess: (res) => {
    AI_get_group.deleteAction(res.data)
  }
})