import { CircularProgress, Grid } from "@mui/material";
import React from "react";

export default function SuspenseFallback() {
  return (
    <Grid
      sx={{
        height: "100%",
        width: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <CircularProgress />
    </Grid>
  );
}
