import React, { useContext, useState } from "react";
import {
  AppBar,
  Avatar,
  Toolbar,
  Typography,
  Tooltip,
  Grid,
  Switch,
  FormControlLabel,
} from "@mui/material";
import AccountCircleRoundedIcon from "@mui/icons-material/AccountCircleRounded";
import { ReactComponent as LogoutIcon } from "assets/LogoutIcon.svg";
import { makeStyles } from "@mui/styles";
// import { useNavigate } from "react-router";
import { Context } from "GlobalState";
import { useNavigate } from "react-router";

const useStyles = makeStyles((theme) => ({
  dmProfileTooltipContainer: {
    "&>div": {
      width: 160,
      borderBottom: `1px solid ${theme.palette.grey[300]}`,
      padding: "4px",
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      cursor: "pointer",
      "&:last-child": {
        borderBottom: "unset",
      },
      "&>div": {
        display: "flex",
        justifyContent: "flex-start",
        alignItems: "center",
        "&>svg": {
          width: 30,
          textAlign: "left",
        },
      },
    },
  },
  dmDarkModeCheckbox: {
    width: "100%",
    margin: "0 !important",
    justifyContent: "space-between",
  },
}));

export default function Header() {
  const classes = useStyles();
  const navigate = useNavigate();

  const [showProfileTooltip, setShowProfileTooltip] = useState(false);

  const handleProfileTooltip = (action) => () => {
    setShowProfileTooltip(action);
  };

  const handleRedirect = (path) => () => {
    navigate(path);
  };

  const { darkMode, setDarkMode } = useContext(Context);

  const handleDarkMode = () => {
    let darkModeActive = window.localStorage.getItem("darkMode") || "light";
    window.localStorage.setItem(
      "darkMode",
      darkModeActive === "light" ? "dark" : "light"
    );
    setDarkMode(darkModeActive === "light" ? "dark" : "light");
  };

  return (
    <AppBar position="static">
      <Toolbar>
        <Typography
          variant="h6"
          component="div"
          sx={{ flexGrow: 1 }}
          onClick={handleRedirect("/home")}
        >
          Date Manager
        </Typography>
        <Tooltip
          disableInteractive={false}
          placement="bottom-end"
          onClose={handleProfileTooltip(false)}
          open={showProfileTooltip}
          disableTouchListener
          onClick={handleProfileTooltip(true)}
          onMouseOver={handleProfileTooltip(true)}
          title={
            <Grid className={classes.dmProfileTooltipContainer}>
              <Grid onClick={handleRedirect("/profile")}>
                <Grid>
                  <Typography variant="body1">Profile</Typography>
                </Grid>
                <AccountCircleRoundedIcon />
              </Grid>
              <Grid>
                <FormControlLabel
                  control={
                    <Switch
                      size="small"
                      id="darkModeSwitch"
                      checked={darkMode === "dark"}
                    />
                  }
                  label="Dark mode"
                  labelPlacement="start"
                  className={classes.dmDarkModeCheckbox}
                  value={darkMode}
                  onClick={handleDarkMode}
                />
              </Grid>
              <Grid onClick={handleRedirect("/login")}>
                <Grid>
                  <Typography variant="body1">Logout</Typography>
                </Grid>
                <LogoutIcon />
              </Grid>
            </Grid>
          }
        >
          <Avatar src={`${process.env.PUBLIC_URL}/assets/profile.png`}>
            PD
          </Avatar>
        </Tooltip>
      </Toolbar>
    </AppBar>
  );
}
