import { useEffect, useMemo, useRef } from 'react'
import { useSelector } from 'react-redux'
import { Store } from 'redux'

/** All the method for HTTP request */
type IHttpMethods = 'GET' | 'POST' | 'PUT' | 'PATCH' | 'DELETE'

/** All the HTTP method props */
interface IHttpFuncProps {
  /** Endpoint of you API */
  endpoint: string
  /** Method for your API */
  method: IHttpMethods
  /** Body params for API */
  params: any
  /** Headers for API */
  headers: any
}

/*
 * @param {String} url
 * @param {variable} variables
 * @returns {string} updated url
 * this method take url and replace inner url with giving variable object
 */
const generateDynamicEndpoint = (url: string, variables: any) => {
  Object.keys(variables || {}).map((i) => {
    url = url.replace(`{${i}}`, variables[i])
  })

  return url
}

