const defaultHttpFn = ({
  endpoint,
  method,
  params,
  headers,
  ...other
}: IHttpFuncProps) => {
  const parseMethod: string = (method || 'get').toLowerCase()

  if (parseMethod === 'get' && typeof params === 'object') {
    const urlParams = new URLSearchParams(params)
    endpoint += `?${urlParams.toString()}`
  }

  return window
    .fetch(endpoint, {
      method: parseMethod,
      headers: headers || {},
      ...(parseMethod !== 'get' && {
        body: params || {}
      }),
      ...(other || {})
    })
    .then((res) => {
      return {
        status: res.status,
        data: res.json()
      }
    })
}

/** Check if any httpFn Defined by user then use that other wise use defaultHttpFn */
const makeHttpCall = (context: any, params: any) => {
  if (context.httpFn) {
    return context.httpFn(params)
  } else {
    return defaultHttpFn(params)
  }
}

/** Interface used for all the allowed props used to generate base instance */
interface ICreateBase {
  /** Redux store of your project */
  store: Store
  /** Base endpoint url of your backend */
  baseApiUrl: string
  /** Redux Dispatch */
  dispatch: Function
  /** Custom httpFn: Used when you make http request */
  httpFn: Function
  /** Middleware functions: executed before/after of every HTTP request. */
  middleware: any
  /** Name of your redux where you wanted to save data.
   * Can be update later for different HTTP Request.
   */
  reducer: 'string'
  /** Default state for your HTTP Request.
   * Can be update later for different HTTP Request.
   */
  defaultState: any

  /** Function used to validate the http response. */
  validateHttpResponse: Function
}

/** Interface used for all the allowed props used to generate api instance */
interface IApiInstance extends ICreateBase {
  /** Endpoint of your HTTP Request */
  endpoint: string
  /** Redux name where you want to save result of HTTP Request. */
  reduxKey: string
  /**
   * Different version of action generated automatically.
   */
  action: string
  /** Unique key from your record
   * can be 'id' or 'parent.child.sub-child' or 'parent[0].child.sub-child'
   */
  key: string
  /** HTTP Request Method
   * GET | Post | PUT | DELETE
   */
  method: IHttpMethods
  /** Callback function executed when HTTP Request Succeed. */
  onSuccess: Function
}

/** Interface for middleware function */
interface IMiddleware {
  /** Callback function executed before HTTP Request happens
   * return result will be passed and saved to redux
   */
  beforeStart?: Function
  /** Callback function executed only success of HTTP Request
   * return result will be passed and saved to redux
   */
  afterSuccess?: Function

  afterUpdate?: Function

  afterDelete?: Function
}

/** CreateBase is used to generate Base Instance */
class CreateBase {
  store: any
  dispatch: Function
  baseApiUrl: string
  httpFn: Function
  middleware: IMiddleware
  reducer: string
  defaultState: any
  validateHttpResponse: Function
  other: any

  constructor(data: ICreateBase) {
    const {
      store,
      baseApiUrl,
      httpFn,
      middleware,
      reducer,
      defaultState,
      validateHttpResponse,
      ...other
    } = data
    this.store = store
    this.dispatch = store.dispatch
    this.baseApiUrl = baseApiUrl || ''
    this.httpFn = httpFn
    this.middleware = middleware
    this.reducer = reducer
    this.defaultState = defaultState
    this.validateHttpResponse = validateHttpResponse
    this.other = other
  }

  /** Used to create HTTP instance from the Base instance */
  createInstance(data: IApiInstance) {
    return new API_INSTANCE({
      ...this.other,
      ...data,
      store: data.store || this.store,
      dispatch: data.dispatch || this.dispatch,
      baseApiUrl: data.baseApiUrl || this.baseApiUrl || '',
      httpFn: data.httpFn || this.httpFn,
      defaultState: data.defaultState || this.defaultState,
      validateHttpResponse:
        data.validateHttpResponse || this.validateHttpResponse
    })
  }

  /** Used to create Listing HTTP instance from the Base instance */
  createListInstance(data: IApiInstance) {
    return new LISTING_API_INSTANCE({
      ...this.other,
      ...data,
      store: data.store || this.store,
      dispatch: data.dispatch || this.dispatch,
      baseApiUrl: data.baseApiUrl || this.baseApiUrl || '',
      httpFn: data.httpFn || this.httpFn,
      defaultState: data.defaultState || this.defaultState,
      validateHttpResponse:
        data.validateHttpResponse || this.validateHttpResponse
    })
  }
}

interface IReduxActions {
  /** <reducerName>_DEFAULT_<yourReduxKey> */
  default: string
  /** <reducerName>_START_<yourReduxKey> */
  start: string
  /** <reducerName>_SUCCESS_<yourReduxKey> */
  success: string
  /** <reducerName>_UPDATE_<yourReduxKey> */
  update: string
  /** <reducerName>_DELETE_<yourReduxKey> */
  delete: string
  /** <reducerName>_ERROR_<yourReduxKey> */
  error: string
}

interface IOtherProps {
  /** Used to refetch data even changing loading to true. */
  refetch?: Boolean
  /** Used to pass Body of HTTP request. */
  params?: any
  /** Used to replace dynamic portion of api endpoint
   * /api/v2/user/:id
   * in above ":id" will be replaced with value of id key from the urlParams object.
   */
  urlParams?: any
}

/** Class used to create HTTP method */
class API_INSTANCE {
  store: any
  dispatch: Function
  baseApiUrl: string
  httpFn: Function
  middleware: IMiddleware
  reducer: string
  defaultState: any
  other: IOtherProps
  reduxKey: string
  actions: IReduxActions
  endpoint: string
  key: string
  apiMethod: IHttpMethods
  onSuccess: Function
  state: any
  validateHttpResponse: Function

  /** Generate dynamic action with combination of different situation. */
  generateActions(ac: string, reducer: string) {
    this.actions = {
      default: `${reducer}_default_` + ac,
      start: `${reducer}_START_` + ac,
      success: `${reducer}_SUCCESS_` + ac,
      update: `${reducer}_UPDATE_` + ac,
      delete: `${reducer}_DELETE_` + ac,
      error: `${reducer}_ERROR_` + ac
    }
  }

  /** Used to get current redux state from the store. */
  getCurrentState() {
    let state = this.store.getState()?.[this.reducer]?.[this.reduxKey]

    if (Array.isArray(state)) {
      state = { ...state }
    } else if (typeof state === 'object') {
      state = { ...state }
    }
    return state
  }

  constructor({
    endpoint,
    reduxKey,
    action,
    key,
    method,
    onSuccess,
    store,
    dispatch,
    baseApiUrl,
    httpFn,
    defaultState,
    middleware,
    reducer,
    validateHttpResponse
  }: IApiInstance) {
    this.store = store
    this.dispatch = dispatch
    this.baseApiUrl = baseApiUrl
    this.httpFn = httpFn
    this.defaultState = defaultState
    this.middleware = middleware
    this.reducer = reducer
    this.generateActions(action, reducer)
    this.reduxKey = reduxKey
    this.endpoint = endpoint
    this.key = key
    this.apiMethod = method || 'GET'
    this.onSuccess = onSuccess
    this.validateHttpResponse = validateHttpResponse
  }

  /** Used to fire HTTP request. */
  call(other: IOtherProps = { refetch: false, params: {}, urlParams: {} }) {
    let startActionPayload = {
      ...this.getCurrentState(),
      ...(!other.refetch && {
        loading: true
      }),
      error: {},
      event: 'start'
    }

    if (typeof this?.middleware?.beforeStart === 'function') {
      startActionPayload = this.middleware.beforeStart({
        currentState: this.getCurrentState(),
        instance: this,
        apiParams: other.params,
        event: 'start'
      })
    }

    this.dispatch({
      type: this.actions.start,
      key: this.reduxKey || '',
      payload: startActionPayload,
      event: 'start'
    })

    try {
      const apiPromise = makeHttpCall(this, {
        endpoint: generateDynamicEndpoint(
          getMergedEndpoint(this.baseApiUrl, this.endpoint),
          other.urlParams || {}
        ),
        method: this.apiMethod,
        headers: {},
        ...(other.params && {
          params: other.params
        })
      })

      apiPromise
        .then(async ({ data: response, status = 200 }: any) => {
          if (this.validateHttpResponse) {
            const isValidResponse = this.validateHttpResponse(response)
            if (!isValidResponse) {
              this.dispatch({
                type: this.actions.error,
                key: this.reduxKey || 'unknown',
                payload: {
                  loading: false,
                  error: response,
                  event: 'error'
                },
                event: 'error'
              })
              return response
            }
          } else if (status > 299) {
            this.dispatch({
              type: this.actions.error,
              key: this.reduxKey || 'unknown',
              payload: {
                loading: false,
                error: response,
                event: 'error'
              },
              event: 'error'
            })
            return response
          }

          if (typeof this.onSuccess === 'function') {
            const onSuccessResult = this.onSuccess(response)
            if (onSuccessResult !== undefined) {
              response = onSuccessResult
            }
          }

          let newPayload = {
            ...this.getCurrentState(),
            loading: false,
            data: response,
            event: 'success'
          }

          if (typeof this?.middleware?.afterSuccess === 'function') {
            newPayload = this.middleware?.afterSuccess({
              currentState: this.getCurrentState(),
              instance: this,
              response: response,
              event: 'success'
            })
          }

          this.dispatch({
            type: this.actions.success,
            key: this.reduxKey || 'unknown',
            payload: newPayload,
            event: 'success'
          })
          return response
        })
        .catch((error: any) => {
          this.dispatch({
            type: this.actions.error,
            key: this.reduxKey || 'unknown',
            payload: {
              loading: false,
              error: error,
              event: 'error'
            },
            event: 'error'
          })
        })
    } catch (error) {
      this.dispatch({
        type: this.actions.error,
        key: this.reduxKey || 'unknown',
        payload: {
          loading: false,
          error: error,
          event: 'error'
        },
        event: 'error'
      })
    }
  }

  /** Used to refetch data without making loading flags to true. */
  refetchAction(other = {}) {
    this.call({ ...other, refetch: true })
  }
}

/** Used to extract value from the object or array or any complex variable
 * path can be 'id' or 'parent.child.sub-child' or 'parent[0].child.sub-child'
 */
const getValueFromPath = (data: any, path: string): string => {
  path = path.replace(/\[(\w+)\]/g, '.$1') // convert indexes to properties
  path = path.replace(/^\./, '') // strip a leading dot
  var a = path.split('.')
  for (var i = 0, n = a.length; i < n; ++i) {
    var k = a[i]
    if (k in data) {
      data = data[k]
    } else {
      return ''
    }
  }
  return data
}

const setValueFromPath = (data: any, path: string, value: any) => {
  path = path.replace(/\[(\w+)\]/g, '.$1') // convert indexes to properties
  path = path.replace(/^\./, '') // strip a leading dot

  var a = path.split('.')
  var o = data
  while (a.length - 1) {
    var n: any = a.shift()
    if (!(n in o)) o[n] = {}
    o = o[n]
  }
  o[a[0]] = value
}

/** It merges the baseApiUrl with current endpoint path */
const getMergedEndpoint = (baseApiUrl: string, endpoint: string) => {
  if (!baseApiUrl) return endpoint

  if (endpoint[0] === '/') {
    return baseApiUrl + endpoint
  }

  return endpoint
}

/**
 *
 * @param {Array} arr
 * @param {Array} deleteArr
 * @param {Object} options
 * @returns will remove record from the arr based on values of deleteArr based on key or callback passed in options
 */
const removeRecordsFromArray = (
  arr: any = [],
  deleteArr: any,
  options: { key?: string; callback: Function }
) => {
  const { key = '', callback } = options
  const deletedFullArray: any = []

  arr = arr.filter((item: any) => {
    const id = callback ? callback(item) : item[key]
    if (deleteArr.includes(id)) {
      deletedFullArray.push(item)
      return false
    } else {
      return true
    }
  })

  return {
    result: arr,
    deleted: deletedFullArray.length,
    deletedItems: deletedFullArray
  }
}

/**
 *
 * @param {Array} array
 * @param {Object} options
 * @returns used to removing duplicate items from array based on key or callback passed in options,
 */
const removeDuplicateFromArray = (
  array: Array<object>,
  options: { key?: string; callback?: Function; excludeSecond?: Boolean }
) => {
  const { key, callback, excludeSecond = false } = options
  const newList = {}
  let duplicates = 0

  array.forEach((item) => {
    let id = callback ? callback(item) : key ? item[key] : ''
    id = id + 'p'
    if (newList[id]) {
      duplicates++
      if (excludeSecond === false) {
        newList[id] = item
      }
    } else {
      newList[id] = item
    }
  })

  return { result: Object.values(newList || {}), duplicates: duplicates }
}

/** It contain all the feature from API_INSTANCE with additional method to delete and update records from the list in redux. */
class LISTING_API_INSTANCE extends API_INSTANCE {
  listPath: string

  constructor(data: any) {
    super(data)
    this.listPath = data.listPath
  }

  updateAction(data: Array<any> | any) {
    try {
      let state = this.getCurrentState()

      const list = [
        ...(this.listPath ? getValueFromPath(state, this.listPath) : state.data)
      ]

      const result = removeDuplicateFromArray(
        [...list, ...(Array.isArray(data) ? data : [data])],
        {
          key: this.key
        }
      )

      if (typeof this?.middleware?.afterUpdate === 'function') {
        state = this.middleware.afterUpdate({
          currentState: this.getCurrentState(),
          instance: this,
          updateData: result
        })
      } else if (this.listPath) {
        setValueFromPath(
          state,
          this.listPath,
          Object.values(result.result || {})
        )
      } else {
        state.data = Object.values(result.result || {})
      }

      this.dispatch({
        type: this.actions.update,
        key: this.reduxKey,
        payload: {
          ...this.getCurrentState(),
          ...state,
          event: 'update'
        },
        event: 'update'
      })
    } catch (error) {
      console.log('Update Action Error', error)
    }
  }

  deleteAction(id: string[] | string | number) {
    try {
      let state = this.getCurrentState()
      const list = [
        ...(this.listPath ? getValueFromPath(state, this.listPath) : state.data)
      ]

      const deleteData = removeRecordsFromArray(
        list,
        Array.isArray(id) ? id : [id],
        {
          callback: (i: any) => getValueFromPath(i, this.key || '')
        }
      )

      if (typeof this?.middleware?.afterDelete === 'function') {
        state = this.middleware.afterDelete({
          currentState: this.getCurrentState(),
          instance: this,
          deleteData: deleteData
        })
      } else if (this.listPath) {
        setValueFromPath(state, this.listPath, deleteData.result)
      } else {
        state.data = deleteData.result
      }

      this.dispatch({
        type: this.actions.delete,
        key: this.reduxKey,
        payload: {
          ...this.getCurrentState(),
          ...state,
          event: 'delete'
        },
        event: 'delete'
      })
    } catch (error) {
      console.log('Delete Error', error)
    }
  }
}

/** This method is used to register dynamic action which is being call from all the instances. */
const bindReducer = (
  reducer: (state: any, action: 'string') => {},
  config: { key: '' }
): any => {
  const path = config.key + '_'

  return (state: any, action: any) => {
    if (action.type.includes(path)) {
      return {
        ...state,
        [action.key]: {
          ...(action.payload || {}),
          ...(action.event && {
            event: action.event
          })
        }
      }
    } else {
      return reducer(state, action)
    }
  }
}

