/** Used to get current state of the HTTP request where detail will be store */
const useInstance = (instance: IApiInstance): any => {
  const currentState = useSelector(
    (state: any) => state?.[instance.reducer]?.[instance.reduxKey]
  )

  const memoedState = useMemo(() => {
    return currentState || instance.defaultState
  }, [currentState])

  return memoedState
}

