import React, { useEffect, useState } from 'react'
import { Chip, Grid, Typography } from '@mui/material';
import { makeStyles } from "@mui/styles";
import { ReactComponent as PencilIcon } from "assets/PencilIcon.svg";
import CreateGroup from 'components/Profile/CreateGroup';
import { AI_get_group } from 'API/groupAPI';
import { useInstance } from 'easy-redux-http-call';
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
  dmGroupContainer: {
    display: "flex",
    flexWrap: "wrap",
    "&>div": {
      margin: theme.spacing(0.5),
    },
    "&>span": {
      width: "30px",
      minWidth: "30px",
      height: "30px",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      borderRadius: "50%",
      border: `1px solid ${theme.palette.grey[400]}`,
      cursor: "pointer",
      margin: theme.spacing(0.5),
    },
  },
  dmGroup: {
    backgroundColor: ({ color }) => color || "black",
    borderColor: ({ color }) => color || "black",
  }
}))

export default function UserGroups(props) {
  const { allowEdit } = props
  const classes = useStyles()
  const [showGroupDialog, setShowGroupDialog] = useState(false);

  const state = useInstance(AI_get_group)

  useEffect(() => {
    AI_get_group.call()
  }, [])

  const handleEdit = (group) => {
    setShowGroupDialog(group)
  }

  return (
    <Grid className={classes.dmGroupContainer}>
      {
        state?.loading ? "loading" :
          state?.data?.length ?
            <>
              {
                state?.data.map((group, i) => (
                  <GroupChip group={group}
                    handleEdit={handleEdit}
                    allowEdit={allowEdit}
                    key={i}
                  />
                ))
              }
              <span
                onClick={() => {
                  setShowGroupDialog(true);
                }}
              >
                +
              </span>
            </>
            :
            <Typography>
              No group created.
              <Link onClick={
                (e) => {
                  e.preventDefault()
                  setShowGroupDialog(true)
                }
              }>
                Click
              </Link>
              to create new group."
            </Typography>
      }


      <CreateGroup
        open={showGroupDialog}
        group={showGroupDialog}
        handleClose={() => setShowGroupDialog(false)}
      />
    </Grid >
  )
}


const GroupChip = ({ group, handleEdit, allowEdit }) => {
  const classes = useStyles({ color: group.color, allowEdit })

  return (
    <Chip
      label={group.name}
      variant="outlined"
      deleteIcon={< PencilIcon width="12" />}
      className={classes.dmGroup}
      onDelete={
        allowEdit ?
          () => handleEdit(group) : null
      }
    />
  )
}