import React, { useRef, useState, useEffect } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  FormHelperText,
  Grid,
  Popper,
  TextField,
  Typography,
} from "@mui/material";
import { useFormik } from "formik";
import { makeStyles } from "@mui/styles";
import { motion, AnimateSharedLayout } from "framer-motion";
import { HexColorPicker } from "react-colorful";
import * as yup from "yup";
import CloseButtonWrapper from "components/Common/Dialog/CloseButtonWrapper";
import { AI_create_group, AI_delete_system, AI_update_system } from "API/groupAPI";
import { useAIEventHandler, useInstance } from "easy-redux-http-call";

const useStyles = makeStyles((theme) => ({
  dmGroupColorContainer: {
    marginTop: 8,
    "&>div": {
      display: "flex",
      justifyContent: "flex-start",
      alignItems: "flex-start",
      "&>p": {
        padding: 6,
      },
      "&>div": {
        display: "flex",
        justifyContent: "flex-start",
        alignItems: "center",
        marginTop: "2px",
        flexWrap: "wrap",
      },
    },
  },
  dmGroupColorCircle: {
    display: "block",
    width: "20px",
    height: "20px",
    borderRadius: "50%",
    margin: "5px",
    position: "relative",
    cursor: "pointer",
    flexShrink: "0",
  },
  dmSelectedGroupColor: {
    position: "absolute",
    top: "-6px",
    left: "-6px",
    right: "-6px",
    bottom: "-6px",
    border: "3px solid white",
    borderRadius: "50%",
  },
  dmCustomColorPickerPopper: {
    padding: 10,
    boxShadow: theme.shadows[5],
    borderRadius: theme.shape.borderRadius,
    zIndex: theme.zIndex.modal + 1,
    backgroundColor: "white",
    "&>button": {
      marginTop: theme.spacing(1),
    },
  },
  dmCustomColorAction: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    borderBottom: "unset !important",
    marginTop: theme.spacing(1),
    width: "100% !important",
    "&>button": {
      margin: theme.spacing(0.5),
    },
  },
}));

const spring = {
  type: "spring",
  stiffness: 500,
  damping: 30,
};

const createGroupSchema = yup.object().shape({
  name: yup.string().required("Required"),
});

export default function CreateGroup(props) {
  const { open, handleClose, group } = props;
  const classes = useStyles();
  const [initialValues, setInitialValues] = useState({
    name: "",
    color: ""
  })
  let groupDetail = group._id ? group : false

  const createGroupState = useInstance(AI_create_group)
  const updateGroupState = useInstance(AI_update_system)
  const deleteGroupState = useInstance(AI_delete_system)


  useEffect(() => {
    if (open) {
      if (groupDetail) {
        setInitialValues({
          name: groupDetail.name,
          color: groupDetail.color
        })
        setSelectedColor(groupDetail.color)
        if (!colors.includes(groupDetail.color)) {
          setColors(color => [...colors, groupDetail.color])
        }
      }
    } else {
      setInitialValues({

      })
      formik.resetForm();
    }
  }, [open]);

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: initialValues,
    onSubmit: (values) => {
      console.log("values", values)
      let params = {
        name: values.name,
        color: selectedColor
      }
      if (groupDetail) {
        AI_update_system.call({
          params: params,
          urlParams: {
            id: groupDetail._id
          }
        })
      } else {
        AI_create_group.call({
          params: params
        })
      }
    },
    validationSchema: createGroupSchema,
  });

  useAIEventHandler(AI_create_group, {
    onSuccess: (res) => {
      handleClose()
    },
    onError: (res) => {
      if (res?.error?.error) {
        formik.setErrors(
          res?.error?.error
        )
      }
    }
  })

  useAIEventHandler(AI_update_system, {
    onSuccess: () => {
      handleClose()
    },
    onError: (res) => {
      if (res?.error?.error) {
        formik.setErrors(
          res?.error?.error
        )
      }
    }
  })

  useAIEventHandler(AI_delete_system, {
    onSuccess: () => {
      handleClose()
    },
    onError: (res) => {
      if (res?.error?.error) {
        formik.setErrors(
          res?.error?.error
        )
      }
    }
  })

  const [colors, setColors] = useState([
    "#ff0055",
    "#0099ff",
    "#22cc88",
    "#ffaa00",
  ]);

  const [selectedColor, setSelectedColor] = useState(colors[0]);
  const [showColorSelectionPopper, setShowColorSelectionPopper] = useState(
    false
  );
  const customColorRef = useRef(null);

  useEffect(() => {
    if (open) {
    } else {
      setShowColorSelectionPopper(false);
    }
  }, [open]);

  const handleAddCustomColor = () => {
    if (!colors.includes(colors)) {
      setColors([...colors, selectedColor]);
    }
    setShowColorSelectionPopper(false);
  };

  const handleDelete = () => {
    if (groupDetail) {
      AI_delete_system.call({
        urlParams: {
          id: groupDetail._id
        }
      })
    }
  }

  return (
    <Dialog open={open} onClose={handleClose}>
      <CloseButtonWrapper onClose={handleClose}>{groupDetail ? "Edit" : "Add"} Group</CloseButtonWrapper>
      <DialogContent>
        <Grid>
          <form id="create-group-from">
            <>
              <TextField
                fullWidth
                autoFocus
                placeholder="Group name"
                label="Group name"
                name="name"
                value={formik.values.name}
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
                helperText={formik.touched.name && formik.errors.name}
                error={
                  formik.touched.name && Boolean(formik.errors.name)
                }
              />
              <Grid className={classes.dmGroupColorContainer}>
                <Typography variant="body2">Color</Typography>
                <Grid>
                  <Grid>
                    <AnimateSharedLayout>
                      {colors.map((color) => (
                        <Item
                          key={color}
                          color={color}
                          isSelected={selectedColor === color}
                          onClick={() => setSelectedColor(color)}
                        />
                      ))}
                    </AnimateSharedLayout>
                  </Grid>
                  <Typography
                    variant="body2"
                    underline
                    ref={customColorRef}
                    onClick={() => setShowColorSelectionPopper(true)}
                  >
                    custom
                  </Typography>
                </Grid>
                {
                  formik?.errors?.color &&
                  <FormHelperText error>
                    {formik.errors.color}
                  </FormHelperText>
                }
                <Popper
                  anchorEl={customColorRef?.current}
                  open={showColorSelectionPopper}
                  className={classes.dmCustomColorPickerPopper}
                  onClose={() => {
                    console.log("Hello");
                  }}
                >
                  <HexColorPicker
                    color={selectedColor}
                    onChange={setSelectedColor}
                  />
                  <Grid className={classes.dmCustomColorAction}>
                    <Button
                      fullWidth
                      onClick={() => {
                        setShowColorSelectionPopper(false);
                      }}
                      variant="outlined"
                    >
                      Cancel
                    </Button>
                    <Button fullWidth onClick={handleAddCustomColor}>
                      Add
                    </Button>
                  </Grid>
                </Popper>
              </Grid>
            </>
          </form>
        </Grid>
      </DialogContent>
      <DialogActions >
        {groupDetail &&
          <Button size="small" color="error"
            style={{
              marginRight: "auto"
            }}
            onClick={handleDelete}
          >
            Delete
          </Button>
        }

        <Grid>
          <Button size="small" variant="string" onClick={handleClose}>
            Cancel
          </Button>
          <Button
            size="small"
            onClick={formik.handleSubmit}
            form="create-group-from"
            type="submit"
            disabled={createGroupState.loading || updateGroupState.loading}
          >
            {
              groupDetail ? "Save" : "Create"
            }
          </Button>
        </Grid>
      </DialogActions>
    </Dialog>
  );
}

function Item({ color, isSelected, onClick }) {
  const classes = useStyles();

  return (
    <span
      className={classes.dmGroupColorCircle}
      onClick={onClick}
      style={{ backgroundColor: color }}
    >
      {isSelected && (
        <motion.div
          layoutId="outline"
          className={classes.dmSelectedGroupColor}
          initial={false}
          animate={{ borderColor: color }}
          transition={spring}
        />
      )}
    </span>
  );
}
