import React, { useEffect, useState } from "react";
import {
  Box,
  Button,
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  Divider,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Switch,
  Tab,
  Tabs,
  TextField,
  Typography,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import { ReactComponent as CircleAddIcon } from "assets/CircleAddIcon.svg";
import ImageRoundedIcon from "@mui/icons-material/ImageRounded";
import CloseButtonWrapper from "components/Common/Dialog/CloseButtonWrapper";
import DesktopDatePicker from "@mui/lab/DesktopDatePicker";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import DateAdapter from "@mui/lab/AdapterMoment";
import { ReactComponent as BirthdayCakeIcon } from "../../assets/BirthdayCake.svg";
import { ReactComponent as AnniversaryIcon } from "../../assets/AnniversaryIcon.svg";
import clsx from "clsx";
import { AI_get_group } from "API/groupAPI";
import { useInstance } from "easy-redux-http-call";

const useStyles = makeStyles((theme) => ({
  dmCreateEventRoot: {
    "&>div": {
      marginBottom: 10,
    },
  },
  dmEventTypeContainer: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    "&>div": {
      boxShadow: "0px 0px 6px -2px rgba(0, 0, 0, 0.25)",
      transition: "box-shadow 0.3s ease-in-out",
      borderRadius: theme.shape.borderRadius,
      display: "flex",
      flexDirection: "column",
      alignItems: "center",
      margin: "5px 3px",
      minWidth: 90,
      padding: "5px 10px",
      alignSelf: "stretch",
      justifyContent: "space-between",
      cursor: "pointer",
      flex: 1,
      "&>svg": {
        height: 63,
      },
    },
    "&>.selectedEvent": {
      boxShadow: `0px 0px 6px -2px rgba(0, 0, 0, 0.25), 0px 0px 0px 4px ${theme.palette.primary.main} inset`,
    },
  },
  dmEventEmojiImageUpload: {
    "&>div": {
      margin: "4px 0px",
    },
  },
  dmEmojiContainer: {
    "&>span": {
      fontSize: 20,
      margin: 4,
      padding: 4,
      borderRadius: 4,
      minWidth: 40,
      width: 40,
      display: "inline-block",
      boxShadow: theme.shadows[5],
      textAlign: "center",
    },
  },
  dmImageUploadContainer: {
    borderRadius: theme.shape.borderRadius,
    background: theme.palette.grey[400],
    padding: 5,
    minHeight: 102,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    marginTop: 8,
  },
  dmImageUploadContent: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  dmCreateEventDivider: {
    margin: "4px 0px",
  },
  dmEventPreferenceContainer: {
    "&>div": {
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
    },
  },
}));

export default function CreateEventDialog(props) {
  const { open, handleClose } = props;
  const classes = useStyles();

  const [coverProfileTypeTab, setCoverProfileTypeTab] = useState("emoji");
  const [eventType, setEventType] = useState("birthday");

  const handleSelectEvent = (event) => () => {
    setEventType(event);
  };

  const groupState = useInstance(AI_get_group)

  useEffect(() => {
    if (open) {
      AI_get_group.call()
    }
  }, [open])


  return (
    <Dialog open={open} onClose={handleClose}>
      <CloseButtonWrapper onClose={handleClose}>
        Create Event
      </CloseButtonWrapper>
      <DialogContent>
        <Grid className={classes.dmCreateEventRoot}>
          <Grid className={classes.dmEventTypeContainer}>
            <Grid
              className={eventType === "birthday" && "selectedEvent"}
              onClick={handleSelectEvent("birthday")}
            >
              <BirthdayCakeIcon />
              <Typography variant="subtitle2">Birthday</Typography>
            </Grid>
            <Grid
              className={
                clsx(
                  eventType === "anniversary" && "selectedEvent",
                  "comming-soon"
                )
              }
              onClick={handleSelectEvent("anniversary")}
            >
              <AnniversaryIcon />
              <Typography variant="subtitle2">Anniversary</Typography>
            </Grid>
            <Grid
              className={
                clsx(
                  eventType === "other" && "selectedEvent",
                  "comming-soon"
                )
              }
              onClick={handleSelectEvent("other")}
            >
              <CircleAddIcon />
              <Typography variant="subtitle2">Other</Typography>
            </Grid>
          </Grid>

          <Grid>
            <TextField
              fullWidth
              label="Name"
              placeholder="John Doe"
            />
          </Grid>
          <Grid>
            <TextField
              fullWidth
              label="Date"
              type="datetime-local"
              value={new Date()}
              placeholder="John Doe"
            />
            <Box display="flex" justifyContent="space-between">
              <FormControlLabel label="Year"
                control={
                  <Checkbox />
                }
              />
              <FormControlLabel label="Time"
                control={
                  <Checkbox />
                }
              />
            </Box >
          </Grid>

          <LocalizationProvider dateAdapter={DateAdapter}>
            <DesktopDatePicker
              fullWidth
              label="Date"
              inputFormat="MM/dd/yyyy"
              // value={value}
              // onChange={handleChange}
              renderInput={(params) => <TextField fullWidth {...params} />}
            />
          </LocalizationProvider>

          <Grid>
            <FormControl fullWidth>
              <InputLabel>Group</InputLabel>
              <Select label="Group">
                {groupState.data?.map(i =>
                  <MenuItem value={i._id}>{i.name}</MenuItem>
                )}

              </Select>
            </FormControl>
          </Grid>

          <Grid className={clsx(
            classes.dmEventEmojiImageUpload,
            'comming-soon'
          )}>
            <Typography grey variant="body2">
              Cover Emoji / Photo
            </Typography>

            <Tabs
              value={coverProfileTypeTab}
              onChange={(e, value) => setCoverProfileTypeTab(value)}
              variant="fullWidth"
              look="ios"
            >
              <Tab disableRipple label="Emoji" value="emoji" />
              <Tab disableRipple label="Image" value="image" />
            </Tabs>

            {coverProfileTypeTab === "emoji" ? (
              <Grid>
                <TextField
                  fullWidth
                  label="Search Emoji"
                  placeholder="Search Emoji"
                />

                <Grid className={classes.dmEmojiContainer}>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                </Grid>
              </Grid>
            ) : (
              <Grid className={classes.dmImageUploadContainer}>
                <Grid className={classes.dmImageUploadContent}>
                  <ImageRoundedIcon />
                  <Typography variant="body2" darkGrey>
                    Drag and Drop or click to upload
                  </Typography>
                </Grid>
              </Grid>
            )}

            <Divider className={classes.dmCreateEventDivider} />

            <Grid className={classes.dmEventPreferenceContainer}>
              <Grid>
                <Typography htmlFor="testing">Remind me</Typography>
                <Switch />
              </Grid>
              <Grid>
                <Grid>
                  <FormControlLabel
                    control={<Checkbox defaultChecked id="testing" />}
                    label="Label"
                  />
                </Grid>
              </Grid>
            </Grid>

            <Divider className={classes.dmCreateEventDivider} />

            <Grid className={classes.dmEventPreferenceContainer}>
              <Grid>
                <Typography>Send email reminder</Typography>
                <Switch />
              </Grid>
            </Grid>

            <Divider className={classes.dmCreateEventDivider} />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button variant="text">Cancel</Button>
        <Button>Create</Button>
      </DialogActions>
    </Dialog>
  );
}
